<template>
  <!-- bidirectional data binding（双向数据绑定） -->
  <div>
    {{content}}
    <el-button @click="toMainRouter">去主路由</el-button>
  </div>
</template>

<script>
  
  export default {
    data () {
      return {
        content: '<h2>I am Example</h2>',
        
      }
    },
    
    methods: {
      toMainRouter(){
        window.location.href = './';
      }
    },

    computed: {
      
    },
    
    mounted() {
      
    }
  }
</script>


